import React, { useEffect, useState } from "react";
import Letter from "./Letter";
import axios from "axios";
import Pagination from "@mui/material/Pagination";

export default function LetterBoard({ socket, limitPerPage }) {
  const [letters, setLetters] = useState([]);
  const [page, setPage] = useState(1); // Current page
  const [totalPages, setTotalPages] = useState(1); // Total pages
  const [isFetching, setIsFetching] = useState(true);

  // Fetch letters when the page changes
  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/letters?page=${page}&limit=${limitPerPage}`)
      .then((response) => {
        setLetters(response.data.letters);
        setTotalPages(response.data.total_pages); // Total pages from backend
        setIsFetching(false);
        window.scrollTo(0, 0);
      })
      .catch((error) => {
        console.error("Error fetching letters:", error);
      });
  }, [page]); // Re-fetch data when the page changes

  useEffect(() => {
    socket.on("newLetter", ({ newLetter, total_pages })  => {
      if (letters.length + 1 > limitPerPage) {
        setLetters([...letters.slice(0, letters.length - 1), newLetter]);
      } 
      else {
        setLetters([...letters, newLetter]);
      }
      setTotalPages(total_pages)
      
    });
  }, [socket, letters]);

  const handlePageChange = (event, value) => {
    setPage(value); // Update the current page
  };

  const convertTime = (timestamp) => {

    const date = new Date(timestamp);
    
    const timeOptions = { hour: "numeric", minute: "numeric", hour12: true };
    const dateOptions = { year: "numeric", month: "short", day: "numeric" };

    // Get current time in the desired format (e.g., "10:30 PM")
    const formattedTime = date.toLocaleTimeString("en-US", timeOptions);

    // Get current date in the desired format (e.g., "Sept 20, 2024")
    const formattedDate = date.toLocaleDateString("en-US", dateOptions);

    // Combine time and date
    const currentTime = `${formattedTime}, ${formattedDate}`;

    return currentTime;
  }

  return (
    <div>
      <div className="letterboard-grid">
        {letters
        .slice()
        .reverse()
          .map((letter) => (
            <Letter
              header={letter["header"]}
              body={letter["body"]}
              signature={letter["signature"]}
              time={convertTime(letter["time"])}
              font={letter["font"]}
            />
          ))}
      </div>
      <div className="pagination">
      {totalPages > 1 &&
        <Pagination
          count={totalPages} // Total number of pages
          page={page} // Current page
          onChange={handlePageChange} // Handler when page changes
          sx={{
            "& .Mui-selected": {
              backgroundColor: "#173660",  // Selected page background color
              color: "#f7f6ef",  // Selected page text color
            },
            "& .MuiPaginationItem-page:hover": {
              backgroundColor: "#173660",  // Hover background color
              color: "#f7f6ef",
            },
          }}
        />
      }
      </div>
    </div>
  );
}
