import React, { useState } from "react";
import PHOTOS from "../utils/photos";
import Card from "./Card";
import Pagination from "@mui/material/Pagination";

export default function Deck() {
  const INITIAL_ROTATION_Z = 40;

  // Set state for current page and items per page
  const [page, setPage] = useState(1);
  const itemsPerPage = 16; // Adjust this based on how many cards you want per page

  // Calculate total number of pages
  const totalPages = Math.ceil(PHOTOS.length / itemsPerPage);

  // Slice the PHOTOS array based on the current page
  const currentPhotos = PHOTOS.slice(
    (page - 1) * itemsPerPage,
    page * itemsPerPage
  );

  // Handle page change
  const handlePageChange = (event, value) => {
    window.scrollTo(0, 0); // Scroll to the top when page changes
    setPage(value); // Set the new page
  };

  return (
    <>
      <div className="deck">
        {currentPhotos.map((photo, index) => (
          <Card
            img={photo["img"]}
            initRotZ={INITIAL_ROTATION_Z * (-1) ** (index % 2)}
            cardText={photo["imgText"]}
          />
        ))}
      </div>

      <div className="pagination">
        {totalPages > 1 && (
          <div className="pagination">
            <Pagination
              count={totalPages}
              page={page}
              onChange={handlePageChange}
              sx={{
                "& .Mui-selected": {
                  backgroundColor: "#173660", // Selected page background color
                  color: "#f7f6ef", // Selected page text color
                },
                "& .MuiPaginationItem-page:hover": {
                  backgroundColor: "#173660", // Hover background color
                  color: "#f7f6ef",
                },
              }}
            />
          </div>
        )}
      </div>
    </>
  );
}
