import React, { useState, useEffect } from "react";
import "../styles/HomeStyle.scss";
import { Link } from "react-router-dom";
import { io } from "socket.io-client";
import LetterBoard from "../components/LetterBoard";
import Footer from "../components/Footer";
import PostButton from "../components/PostButton";
import Intro from "../components/Intro";
import ShareButton from "../components/ShareButton";

export default function LetterPage() {
  const [loading, setLoading] = useState(true);
  const [socketInstance, setSocketInstance] = useState(null);
  const [introComplete, setIntroComplete] = useState(false);

  const LIMIT_PER_PAGE = 15;

  const handleIntroEnd = () => {
    setIntroComplete(true); // Show the main content after intro completes
  };

  useEffect(() => {
    if (loading === true) {
      const socket = io(process.env.REACT_APP_API_URL+"/", {
        transports: ["websocket"],
      });

      setSocketInstance(socket);

      socket.on("connect", (data) => {
        setLoading(false);
        console.log(data);
      });

      socket.on("disconnect", (data) => {
        setLoading(false);
        console.log(data);
      });

      return function cleanup() {
        setLoading(false);
        socket.disconnect();
      };
    }
  }, []);

  return (
    <div>
      <Intro tag={"LETTERBOARD"} onAnimationEnd={handleIntroEnd} />
      <div className={`hidden-content ${introComplete ? "visible" : ""}`}>
        <ShareButton />
        <Link to="/">
          <div className="home-btn">Home</div>
        </Link>

        {/* <div className='letterpage-header'>Letters From Your Loved Ones</div> */}
        <div className="letterpage-header">LETTERBOARD</div>
        <div className="letterpage-subheader">
          Random thoughtful words maybe I don't know yet
        </div>
        {!loading && (
          <LetterBoard socket={socketInstance} limitPerPage={LIMIT_PER_PAGE} />
        )}
        <PostButton socket={socketInstance} limitPerPage={LIMIT_PER_PAGE} />
        <Footer text={"Glad to have been your ok friend"}/>
      </div>
    </div>
  );
}
